@import "../../common/styles/scss/var.scss";
@import "../../common/styles/scss/mixins";
.main {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 300px;
  margin-right: 10px;
	a {
		text-decoration: none;
		color: $bgColor;
		transition: 0.3s;
		position: relative;
		&:hover {
			color: $hoverTextColor;
			transition: 0.3s;
			&::after {
				width: 100%;
				transition: 0.3s;
			}
		}
		&::after {
			@include showBottomHoverLine();
		}
	}
}