.main {
  flex-direction: column;
  flex-wrap: nowrap;
	.container {
		display: flex;
		flex-direction: column;
		.projectItems {
			width: 100%;
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			align-items: center;
			flex-wrap: wrap;
			gap: 10px;
			margin-top: 10px;
			@media screen and (max-width: 1330px){
				max-width: 730px;
			}
		}
	}
}




